.wrap{
    background-color: #202020;
    height: 100vh;
}
.arrow:hover{
    background-color: #323232;
}
.arrow{
    height: 100vh;
    color: #fff;
    padding: 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: .3s linear;
}
.panel{
    position: absolute;
    right: 41px;
    background-color: #202020;
    height: 100vh;
}
@media(min-width: 1701px){
    .panel{
        top: 0;
    }
    .arrow{
        border-left: 1px solid #242424;
    }
}
@media(max-width:1700px){
    .wrap{
        height: 40px;
        width: 100vw;
        padding: 0px;
        justify-content: center;
        align-items: center;
    }
    .arrow{
        height: 40px;
        justify-content: center;
    }
    .panel{
        left: 0;
        bottom: 40px;
        height: 40vh;
        border: 1px solid #3c3c3c;
        background-color: #1a1a1a;
        width: 100vw;
        display: flex;
        align-items: center;
        color: #fff;
        justify-content: center;
    }
    .forward{
        transform: rotate(90deg)
    }
    .back{
        transform: rotate(90deg);
    }
}
