.wrap{
    display: flex;
    flex-wrap: wrap;
    flex-basis: 100%;
    padding-bottom: 1px;
}
.wrap div{
    background-color: #141414;
}
.one{
    width: 100%;
    height: 100%;
    display: flex;
}
.one div div{
    border: none;
}
.two_v{
    display: flex;
    align-items: center;
    flex: 1 1 0
}
.two_h{
    display:flex;
    flex-direction: column;
    flex: 1 1 0;
}
.four{
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    width: 100%;
}
@media(max-width:1700px){
    /* .wrap{
        padding-bottom: 39px;
    } */
}
@media(min-width:2000px){
    .two_h{
        flex-direction: row;
    }
}